*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html[data-theme="light"] {
  --main-text-color: #111;
  --secondary-text-color: rgba(17, 17, 17, .6);
  --subtitle-color: #b4afaf;
  --congrats-text-color: rgba(17, 17, 17, .5);
  --bg-color: #f6f6f6;
  --btn-grad: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  --backdrop: #f6f6f6;
  --card-color: #fff;
  --amazon-color: grayscale(100%);
  --icon-shadow: drop-shadow(0 3px .85px rgba(0, 0, 0, .15));
}

html[data-theme="dark"] {
  --main-text-color: #fff;
  --secondary-text-color: rgba(255, 255, 255, .6);
  --subtitle-color: #b4afaf;
  --congrats-text-color: rgba(255, 255, 255, .5);
  --bg-color: #202024;
  --btn-grad: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
  --backdrop: #202024;
  --card-color: #111;
  --amazon-color: brightness(0) invert(1);
  --icon-shadow: drop-shadow(0 3px .85px #fff);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-family: DM Sans;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(DM Sans Regular), local(DMSans-Regular), url("DMSans-Regular.461ab9f9.woff2") format("woff2"), url("DMSans-Regular.c108cb9e.woff") format("woff"), url("DMSans-Regular.2d019d46.ttf") format("truetype"), url("DMSans-Regular.3782c94e.eot") format("embedded-opentype");
}

@font-face {
  font-family: DM Sans;
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local(DM Sans Medium), local(DMSans-Medium), url("DMSans-Medium.63db2800.woff2") format("woff2"), url("DMSans-Medium.7d344570.woff") format("woff"), url("DMSans-Medium.7638e628.ttf") format("truetype"), url("DMSans-Medium.5d1afa82.eot") format("embedded-opentype");
}

@font-face {
