}

.container {
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
}

.categories {
  max-width: 335px;
  height: 228px;
  scroll-behavior: smooth;
  margin-top: 40px;
  margin-bottom: 40px;
  overflow-y: scroll;
}

.categories::-webkit-scrollbar {
  width: 8px;
  height: 114px;
  background-color: var(--backdrop);
}

.categories::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  border-radius: 12px;
}

.title-categories {
  color: #4f2ee8;
  max-width: 335px;
  height: 40px;
  z-index: 100;
  background-color: var(--backdrop);
  margin: 0;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.13;
  display: block;
  position: absolute;
}

.categories-list {
  margin-top: 0;
  padding: 42px 0 0;
  list-style: none;
  overflow: hidden;
}

.list-js {
  color: var(--secondary-text-color);
  height: 18px;
  margin-bottom: 24px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.13;
  display: flex;
}

.categories-span-js {
  color: var(--secondary-text-color);
}

.categories-span-js:focus, .categories-span-js:hover, .categories-span-js:active {
  color: #4f2ee8;
  text-transform: uppercase;
  white-space: nowrap;
  font-size: 4.3vw;
  font-weight: 700;
  line-height: 1.13;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
  }

  .categories-span-js:focus, .categories-span-js:hover, .categories-span-js:active {
    font-size: 15px;
    line-height: 1.33;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: none;
    width: 768px;
  }

  .categories {
    width: 337px;
    height: 472px;
    margin-top: 0;
    margin-bottom: 0;
  }

  .categories::-webkit-scrollbar {
    width: 8px;
    height: 167px;
  }

  .title-categories {
    width: 337px;
    font-size: 18px;
    line-height: 1.33;
  }

  .categories-list {
    padding-top: 52px;
  }

